import $ from 'jquery';
// https://stackoverflow.com/questions/44932576/jquery-ui-sortable-is-not-a-function-with-webpack
import 'jquery-ui/ui/widgets/resizable';
import 'jquery-ui/themes/base/base.css';
import 'jquery-ui/themes/base/resizable.css';

import L from 'leaflet';
import 'leaflet/dist/leaflet.css';
// The leaflet-defaulticon-compatibility plugin fixes marker icons when using webpack.
// See: https://github.com/Leaflet/Leaflet/issues/4968#issuecomment-399857656
import 'leaflet-defaulticon-compatibility/dist/leaflet-defaulticon-compatibility.webpack.css';
import 'leaflet-defaulticon-compatibility';

import 'leaflet-tilelayer-swiss';

import 'leaflet.heightgraph';
import 'leaflet.heightgraph/dist/L.Control.Heightgraph.min.css';

import '@mrubli/leaflet-control-maximize';

import toGeoJSON from '@mapbox/togeojson';

// Creates a map element, optionally with tracks and a height graph.
//
// options:
// {
//   element: string,		ID of the <div> tag containing the map.
//   layers: {              Layer options. Optional.
//     openCycleMap: {      https://www.thunderforest.com/maps/opencyclemap/
//       apiKey: string,    API key for OpenCycleMap
//     },
//     outdoors: (ditto),   https://www.thunderforest.com/maps/outdoors/
//     landscape: (ditto),  https://www.thunderforest.com/maps/landscape/
//     enabled: [],         List of layer IDs to enable. Default: [ 'openStreetMap' ]
//     position: string,	Position of the layer selector. One of ['topleft', 'topright', 'bottomleft' (default), 'bottomright' ].
//   },
//   scale: {               Options for the scale. Optional.
//     enabled: bool,		Whether to show a scale. Setting this to false is like omitting 'scale'.
//     position: string,	Position of the scale. One of ['topleft', 'topright', 'bottomleft', 'bottomright' (default)].
//   },
//   maximize: {
//     enabled: bool		Whether to show the maximize button. Default: true
//   },
//   center: point,			Center point of the map as [x, y] or { lat: x, lon: y }. Optional iif tracks or markers are given.
//   zoom: int,				Zoom level of the map. Optional iif tracks or markers are given.
//   tracks: [],			Array of GPX paths to display as tracks. Optional.
//   waypoints: {			Options for waypoint rendering in tracks. Optional.
//     enabled: bool,		Whether to render track waypoints. Optional. Default: false
//   }
//   markers: [],           Array of [lat, lon] tuples. Optional.
//   heightgraph: {			Options for the height graph (only supported for a single track). Optional.
//     enabled: bool,		Enable/disable the height graph. Setting this to false is like omitting 'heightgraph'.
//     expanded: bool,		Expand the height graph upon loading. Default: true
//     width: int,			Width of the height graph. Default: 360
//     height: int,			Height of the height graph. Default: 180
//     resizable: bool,		Make the height graph resizable. Default: false
//     minWidth: int,		Minimum width of the height graph if resizable.
//     minHeight: int,		Minimum height of the height graph if resizable.
//     maxWidth: int,		Maximum width of the height graph if resizable.
//     maxHeight: int,		Maximum height of the height graph if resizable.
//   }
// }
Quip.createTrackMap = function(options) {
	const tracks = options.hasOwnProperty('tracks') ? options.tracks : [];

	// Create all enabled map layers (or the default one if none were given)
	if(!options.hasOwnProperty('layers')) {
		options.layers = {};
	}
	const layers = getMapLayers(options);

	// Create the map with the default layer
	var map = L.map(options.element, {
		layers: [ layers.baseLayers[layers.defaultLayerName] ],
		crs: layers.crs || L.CRS.EPSG3857,
	});

	// If there are multiple layers add a map layer selection control
	if(Object.keys(layers.baseLayers).length > 1 || Object.keys(layers.overlays).length > 1) {
		L.control.layers(layers.baseLayers, layers.overlays, { position: options.layers.position || 'bottomleft' })
			.addTo(map);
	}

	// Add a map scale control if enabled
	if(options.scale && options.scale.enabled) {
		L.control.scale({
			position: options.scale.position || 'bottomright',
			imperial: false,
		}).addTo(map);
	}

	// Add a maximize button unless disabled
	if (!options.maximize || options.maximize.enabled) {
		L.control.maximize().addTo(map);
	}

	// For now we don't support heightgraphs if there are multiple tracks
	if(tracks.length != 1)
		options.heightgraph = null;

	var output = {
		bounds: L.latLngBounds(),	// Combined bounds of all tracks
	};
	var reqs = [];		// Array of jqXHR objects for the track downloads
	for(const source of tracks) {
		// Retrieve the source document.
		// For now we only support GPX, so we insist on an 'xml' data type, which means JQuery is going to convert the
		// response string into an XML DOM even if the server does not send the correct MIME type (application/gpx+xml).
		// We could also manually convert if necessary (see https://github.com/mapbox/togeojson#protips):
		//   gpxData = (new DOMParser()).parseFromString(gpxData, 'text/xml');
		reqs.push(
			$.ajax(source, { dataType: 'xml' })
				.done(function(gpxData) {
					addGpxToMap(gpxData, map, options, output);
				})
				.fail(function(jqXHR, textStatus, errorThrown) {
					const error = "ERROR: Failed to retrieve track '" + source + "': " + textStatus + ": " + errorThrown;
					console.log(error);
					// If there are multiple errors the line below may overwrite other errors but the when(...).fail()
					// callback gets called as soon as the first request fails. We're okay with that.
					output.error = error;
				})
		);
	}

	// Add markers if given
	const markers = options.hasOwnProperty('markers') ? options.markers : [];
	for(const marker of markers) {
		L.marker(marker).addTo(map);
		output.bounds.extend(L.latLng(marker));
	}

	// Once all layers have been added (and we know the track bounds) set the map view
	$.when.apply(null, reqs)
		.then(function() {
			// Zoom in/out on the bounds of the layer (unless a specific center point was given)
			if(options.zoom && options.center) {
				map.setView(options.center, options.zoom);
			}
			else if(output.bounds.isValid()) {
				map.fitBounds(output.bounds);
			}
		})
		.fail(function() {
			map.remove();
			$('#' + options.element).text(output.error);
		})
	;

	return map;
};


function getMapLayers(options, bounds) {
	const KnownLayers = new Map([
		[ 'org.openstreetmap.standard', {
			name: 'OpenStreetMap',
			url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
			attribution: 'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors',
			makeLayer: (_key, info, _options) => L.tileLayer(info.url, { attribution: info.attribution }),
		} ],
		[ 'com.thunderforest.cycle', {
			name: 'OpenCycleMap',
			url: 'https://tile.thunderforest.com/cycle/{z}/{x}/{y}.png?apikey={apikey}',
			attribution: 'Maps &copy; <a href="https://www.thunderforest.com/">Thunderforest</a>. Data &copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors.',
			makeLayer: (_key, info, options) => L.tileLayer(info.url, { attribution: info.attribution, apikey: options.apiKey }),
		} ],
		[ 'com.thunderforest.outdoors', {
			name: 'Outdoors',
			url: 'https://tile.thunderforest.com/outdoors/{z}/{x}/{y}.png?apikey={apikey}',
			attribution: 'Maps &copy; <a href="https://www.thunderforest.com/">Thunderforest</a>. Data &copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors.',
			makeLayer: (_key, info, options) => L.tileLayer(info.url, { attribution: info.attribution, apikey: options.apiKey }),
		} ],
		[ 'com.thunderforest.landscape', {
			name: 'Landscape',
			url: 'https://tile.thunderforest.com/landscape/{z}/{x}/{y}.png?apikey={apikey}',
			attribution: 'Maps &copy; <a href="https://www.thunderforest.com/">Thunderforest</a>. Data &copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors.',
			makeLayer: (_key, info, options) => L.tileLayer(info.url, { attribution: info.attribution, apikey: options.apiKey }),
		} ],
		// SwissTopo layers: https://leaflet-tilelayer-swiss.karavia.ch/layer_list
		[ 'ch.swisstopo.pixelkarte-farbe', {
			name: 'SwissTopo National maps',
			crs: L.CRS.EPSG2056,	// Swiss CH1903+/LV95
			makeLayer: (key, _info, _options) => L.tileLayer.swiss({ layer: key }),
		} ],
		[ 'ch.swisstopo.swissimage', {
			name: 'SwissTopo Aerial imagery',
			crs: L.CRS.EPSG2056,	// Swiss CH1903+/LV95
			makeLayer: (key, _info, _options) => L.tileLayer.swiss({ layer: key, maxNativeZoom: 28 }),
		} ],
		[ 'ch.swisstopo.swisstlm3d-wanderwege', {
			name: 'Hiking trails',
			overlay: true,
			crs: L.CRS.EPSG2056,	// Swiss CH1903+/LV95
			makeLayer: (key, _info, _options) => L.tileLayer.swiss({ layer: key, maxNativeZoom: 26, format: 'png', opacity: 0.7 }),
		} ],
		[ 'ch.astra.mountainbikeland', {
			name: 'Mountainbikeland Schweiz',
			overlay: true,
			crs: L.CRS.EPSG2056,	// Swiss CH1903+/LV95
			makeLayer: (key, _info, _options) => L.tileLayer.swiss({ layer: key, maxNativeZoom: 26, format: 'png', opacity: 0.7 }),
		} ],
		[ 'ch.astra.veloland', {
			name: 'Veloland Schweiz',
			overlay: true,
			crs: L.CRS.EPSG2056,	// Swiss CH1903+/LV95
			makeLayer: (key, _info, _options) => L.tileLayer.swiss({ layer: key, maxNativeZoom: 26, format: 'png', opacity: 0.7 }),
		} ],
		[ 'ch.swisstopo.schneeschuhwandern', {
			name: 'Snowshoe trekking',	// Purple trails
			overlay: true,
			crs: L.CRS.EPSG2056,	// Swiss CH1903+/LV95
			makeLayer: (key, _info, _options) => L.tileLayer.swiss({ layer: key, maxNativeZoom: 26, format: 'png', opacity: 0.7 }),
		} ],
		[ 'ch.swisstopo-karto.schneeschuhrouten', {
			name: 'Snowshoe routes',
			overlay: true,
			crs: L.CRS.EPSG2056,	// Swiss CH1903+/LV95
			makeLayer: (key, _info, _options) => L.tileLayer.swiss({ layer: key, maxNativeZoom: 25, format: 'png', opacity: 0.7 }),
		} ],
	]);

	var layers = {
		defaultLayerName:	undefined,
		baseLayers:			{},
		overlays:			{},
		// The default CRS in Leaflet is L.CRS.EPSG3857 (WGS 84), which is used by OSM and the like.
		// That default is represented by the value 'null'.
		// We start off with 'undefined' and the first requested layer defines the active CRS.
		crs:				undefined,
	};

	// Use the first known layer if no layers were requested
	var requestedLayers = options.layers.enabled || [];
	if(requestedLayers.length == 0) {
		requestedLayers.push([...KnownLayers][0][0]);	// Add the first key of the KnownLayers table
	}

	for(const requestedKey of requestedLayers) {
		// Look up the requested layer key in our table of known layers
		const knownLayer = KnownLayers.get(requestedKey);
		if(!knownLayer) {
			console.log("Warning: Ignoring unknown map layer name: " + requestedKey);
			continue;
		}

		// Skip layers whose CRS is different from the first layer's CRS.
		// The reason for this is that Leaflet does not currently allow mixing projections, although
		// it seems possible with some tricks: https://github.com/Leaflet/Leaflet/issues/2553
		const knownLayerCrs = knownLayer.crs || null;
		if(layers.crs === undefined)
			layers.crs = knownLayerCrs;
		if(knownLayerCrs != layers.crs) {
			const name = crs => crs ? crs.code : L.CRS.EPSG3857.code;
			console.log("Warning: Skipping map layer because of CRS mismatch: " +
				requestedKey + " (layer: " + name(knownLayer.crs) + ", current: " + name(layers.crs) + ")");
			continue;
		}

		// Obtain the requested options and use blank defaults
		const requestedOptions = options.layers[requestedKey] || {};

		// Error out if a layer requires an API key but none was supplied
		if(knownLayer.url && knownLayer.url.includes('{apikey}') && !requestedOptions.apiKey) {
			console.log("ERROR: No API key for map layer: " + requestedKey);
			continue;
		}

		// Create the layer
		const layer = knownLayer.makeLayer(requestedKey, knownLayer, requestedOptions);
		if(knownLayer.overlay)
		{
			layers.overlays[knownLayer.name] = layer;
		}
		else
		{
			layers.baseLayers[knownLayer.name] = layer;
			if(!layers.defaultLayerName)
				layers.defaultLayerName = knownLayer.name;
		}
	}

	if(!layers.defaultLayerName) {
		throw new Error("No valid layers given");
	}
	return layers;
}


function addGpxToMap(gpxData, map, options, output) {
	// Convert the GPX to GeoJSON. The resulting document typically looks like this:
	//
	//	{
	//	  "type": "FeatureCollection",
	//	  "features": [
	//	    {
	//	      "type": "Feature",
	//	      "properties": {
	//	        "name": "18-06-11 18:06:28 Bus",
	//	        "time": "2011-06-18T06:57:29Z",
	//	        "coordTimes": [
	//	          "2011-06-18T06:57:29Z",
	//	          "2011-06-18T06:57:39Z",
	//	          ...
	//	          "2011-06-18T10:06:26Z"
	//	        ]
	//	      },
	//	      "geometry": {
	//	        "type": "LineString",
	//	        "coordinates": [
	//	          [ 121.57447099685669, 25.183594962581992, 488.97 ],
	//	          [ 121.57460602931678, 25.183306960389018, 491.37 ],
	//	          ...
	//	          [ 121.51655196212232, 25.043217027559876,  54.45 ]
	//	        ]
	//	      }
	//	    },
	//	    {
	//	        "type": "Feature",
	//	        "properties": {
	//	            "name": "LAP001",
	//	            "cmt": "LAP001",
	//	            "desc": "LAP001"
	//	        },
	//	        "geometry": {
	//	            "type": "Point",
	//	            "coordinates": [ 9.589223452, 47.019925425 ]
	//	        }
	//	    },
	//	    
	//	  ]
	//	}
	//
	// At the minimum there should be a feature with a LineString geometry, which is the track itself.
	// However, there can also be features with Point geometries, which are waypoints. See below.
	var geoJsonData = toGeoJSON.gpx(gpxData);
	//console.log(geoJsonData);

	// Remove non-waypoint data unless track waypoint rendering is explicitly enabled
	if (!options.waypoints || !options.waypoints.enabled) {
		removeNonWaypointData(geoJsonData);
	}

	// Certain properties are necessary for Leaflet.Heightgraph to load the data correctly, so add these
	geoJsonData.properties = { summary: "elevation" };
	geoJsonData.features[0].properties.attributeType = '0';		// used for color mappings

	// Function that cycles through a list of colors on every invocation
	const getNextColor = (function() {
		var currentColorIndex = 0;
		return function() {
			const colors = [
				'DeepPink',
				'DarkGreen',
				'MediumBlue',
				'DarkCyan',
				'DarkOrchid',
				'DarkOrange',
				'Black',
			];
			const nextColor = colors[currentColorIndex];
			currentColorIndex = (currentColorIndex + 1) % colors.length;
			return nextColor;
		}
	})();

	// Add the GeoJSON to the map
	const geoJsonLayerOptions = {
		style: function(feature) {
			return {
				color: getNextColor(),
				opacity: 0.7,
			};
		}
	};
	const geoJsonLayer = L.geoJSON(geoJsonData, geoJsonLayerOptions)
		.addTo(map);

	// Extend the bounds to include this track
	if(!options.center && !options.zoom) {
		output.bounds.extend(geoJsonLayer.getBounds());
	}

	// Create a height graph
	if(options.heightgraph) {
		// Leaflet.Heightgraph chokes on waypoints, so we filter everything out that's not a track.
		removeNonWaypointData(geoJsonData);

		addHeightgraphToMap(geoJsonData, map, options);
	}
}


function addHeightgraphToMap(geoJsonData, map, options) {
	if(!options.heightgraph.enabled)
		return;

	const width  = options.heightgraph.width  || 360;
	const height = options.heightgraph.height || 180;

	// Create the height graph and add it to the map
	var hg = L.control.heightgraph({
		expand: options.heightgraph.expanded === undefined || options.heightgraph.expanded,
		position: 'topright',
		width: width,
		height: height,
		margins: { left: 60, top: 15, right: 35, bottom: 30 },
		highlightStyle: { color: 'black', opacity: 0.3, weight: 6 },
		mappings: {
			'elevation': {				// references the FeatureCollection with properties.summary == 'elevation'
				'0': {					// features with properties.attributeType = '0'
					text: '',			// "Type" displayed in the popup
					color: '#87CEFA'	// graph color
				}
			}
		}
	});
	hg.addTo(map);					// Must be added to the map before adding the data
	hg.addData([ geoJsonData ]);	// Expects an array of "FeatureCollection"s

	// Make the height graph resizable if requested
	if(options.heightgraph.resizable) {
		$('#' + options.element + ' .heightgraph').resizable({
			handles: 'w, s, sw',
			start: function(event, ui) {
				//console.log("start resize: ", ui.size, ui.position)
			},
			stop: function(event, ui) {
				//console.log("stop  resize: ", ui.size, ui.position);
				// Remove the size/position of the UI element (.heightgraph .leaflet-control) because
				// it should be sized dynamically based on its contents. Giving it a fixed size causes
				// the toggle icon to be in the wrong place when the height graph is minimized.
				ui.element.css({ "width": "", "height": "", "left": "", "top": "" });
			},
			resize: function(event, ui) {
				//console.log("during resize: ", ui.size, ui.position);
				if(ui.originalPosition.left != ui.position.left) {
					// Left edge was dragged => Only keep width change since we're sticking to the right
					ui.position.left = 0;
				}
				hg.resize(ui.size, ui.position);
			},
			minWidth:  options.heightgraph.minWidth  || (width  / 2),
			minHeight: options.heightgraph.minHeight || (height / 2),
			maxWidth:  options.heightgraph.maxWidth  || (width  * 2),
			maxHeight: options.heightgraph.maxHeight || (height * 2),
		});
	}
}


function removeNonWaypointData(geoJsonData) {
	// Remove everything that isn't a LineString (i.e. track)
	geoJsonData.features = geoJsonData.features.filter(
		feature => feature.geometry.type == 'LineString'
	);
}
